@font-face {
  font-family: "Raleway-ExtraBold";
  src: url('data:application/font-woff;charset=utf-8;base64,') format("woff");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Raleway-Medium";
  src: url('data:application/font-woff;charset=utf-8;base64,') format("woff");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "Raleway-SemiBold";
  src: url('data:application/font-woff;charset=utf-8;base64,') format("woff");
  font-style: normal;
  font-weight: normal;
}

body {
  overflow: auto;
}

::selection {
  background-color: $hover-color;
  color: $white;
}

::-moz-selection {
  background-color: $hover-color;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: $hover-color;
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
  background-color: #998675;
}

/*header*/

.head {
  @include bgi('photo.png');
  width: 100%;
  height: 1024px;

  &:before {
    content: "";
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgba(17,17,17,.7);
  }
}

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  padding-top: 40px;
}

.logo {
  background-position: 0 -244px;
  height: 56px;
  text-indent: -9999px;
  width: 186px;
}

.head-content {
  padding-top: 20px;
}

.menu {
  margin-right: 62px;

  &__item {

    display: inline-block;

    &:not(:last-child) {
      margin-right:25px;
    }
  }

  &__link {
    display: inline-block;
    text-decoration: none;
    color: $white;
    @include transition-mixin();
    &.active {
      color: $hover-color;
      cursor: default;
    }
  }
}

.cart-icon,
.search-icon {
  position: relative;
  width: 26px;

  &:before,
  &:after {
    content: "";
    bottom: -8px;
    height: 30px;
    position: absolute;
    left: 0;
    @include transition-mixin(opacity);
    width: 100%;
  }
}

.cart-icon {

  margin-right: 25px;

  &:before {
    background-position: -88px -180px;
  }

  &:after {
    background-position: -88px -213px;
  }

}

.search-icon:before {
  background-position: -116px -180px;
}

.search-icon:after {
  background-position: -116px -213px;
}

.cart-icon:after,
.search-icon:after {
  opacity: 0;
}

.head-bottom {
  display: table;
  height: calc(100% - 96px);
  margin: auto;

  .slogan {
    max-width: 592px;
    text-align: center;
    display: table-cell;
    vertical-align: middle;
  }
}

.white-color{
  color: $white;
}

.secondary-title {
  font-size: 36px;
  font-family: Raleway-Medium, sans-serif;
  padding-bottom: 16px;
}

.title {
  font-size: 52px;
  font-family: Raleway-ExtraBold, sans-serif;
  padding-bottom: 20px;
}

.secondary-text {
  line-height: 24px;
  padding-bottom: 50px;
}

.btn {
  display: inline-block;
  min-width: 170px;
  vertical-align: top;
  font-family: Raleway-ExtraBold, sans-serif;
  background-color: #998675;
  box-shadow: 0 3px 0 rgba(115,99,87,1);
  @include transition-mixin();
  border-radius: 4px;
}

button.btn {
  cursor: pointer;
  border: none;
  height: 55px;
}

a.btn {
  text-align: center;
  text-decoration: none;
  padding-top: 20px;
  padding-bottom: 20px;
}


/*main*/

.services {
  padding-top: 80px;
  padding-bottom: 70px;
  border-bottom: 2px solid $hover-color;
}

.services__department {
  display: inline-block;
  max-width: 256px;
  text-align: center;
  padding-top:78px;
  padding-bottom: 32px;
  position: relative;

  &:before,
  &:after{
    content: "";
    position: absolute;
    left:0;
    right: 0;
    margin: auto;
  }

  &:before {
    height: 50px;
    width: 56px;
    top:0;
  }

  &:after {
    height: 2px;
    width: 72px;
    bottom: 0;
    background-color: #f1eee9;
    @include transition-mixin();
  }
}

.services__department:not(:last-child) {
  margin-right: 44px;
}

.services__department--design:before {
  background-position: 0 -128px;
}

.services__department--development:before {
  background-position: -58px -128px;
}

.services__department--customization:before {
  background-position: -116px -128px;
}

.services__department--marketing:before {
  background-position: -174px -128px;
}

.services__department--title {
  font-family: Raleway-SemiBold, sans-serif;
  color: $light-grey;
  @include transition-mixin();
  padding-bottom: 20px;
}

.basic-text {
  line-height: 24px;
  color: $grey;
}

//latest-projects-section

.latest-projects-title {
  font-size: 30px;
  font-family: Raleway-SemiBold, sans-serif;
  color: $light-grey;
  text-align: center;
  padding-bottom: 22px;
}

.latest-projects-secondary-text {
  line-height: 28px;
  color: $grey;
  max-width: 770px;
  margin: auto;
  text-align: center;
  padding-bottom: 40px;
}

.categories {
  text-align: center;
  padding-bottom: 40px;

  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 7px;
    }
  }

  &__link {
    font-size: 14px;
    line-height: 21px;
    color: $grey;
    padding: 10px 18px;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    @include transition-mixin();

    &.active {
      background-color: #998675;
      color: $white;
      cursor: default;
    }
  }
}

/*projects*/

.projects-container {
  margin-left: -15px;
  margin-right: -15px;
}

.image-block {
  width: 33%;
  padding-left: 15px;
  padding-right: 15px;
}

.image-block:not(:nth-last-child(-n+3)) {
  margin-bottom: 30px;
}

.image-link {

  .pic-box {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(11, 11, 11, .7);
      @include transition-mixin();
      opacity: 0;
    }
  }

  .text-box {
    padding: 30px 0 28px 30px;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: -10px;
      left: 20px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 14px 10px 14px;
      border-color: transparent transparent #fff transparent;
    }

    .image-info {
      padding-bottom: 15px;
      font-family: Raleway-Medium, sans-serif;
      color: $hover-color;
    }

    .image-about {
      line-height: 20px;
      color: #d1d1d1;
    }
  }
}


/*features section*/


.features {
  background-color: #fbfaf8;
  padding-top: 80px;
}

.features-inner {
  position: relative;
  padding-top: 0;
  min-height: 616px;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    height: 616px;
    width: 423px;
    left: 15px;
    background: url(../images/iPhone-in-hand.png) no-repeat;
  }
}

.info-box {
  width: 660px;
  padding-top: 118px;
}

.info-box-title {
  font-size: 30px;
  font-family: Raleway-SemiBold, sans-serif;
  color: $light-grey;
  padding-bottom: 40px;
}

.info-box-paragraph {
  line-height: 28px;
  color: $grey;
  padding-bottom: 40px;
}

.little-list {
  margin-left: 50px;
}

.list-text {
  color: $grey;
  padding-bottom: 15px;
}


.list-item {
  padding-left: 50px;

  &:before {
    content: "";
    position: absolute;
    top:0;
    left:0;
    height: 25px;
    width: 25px;
    background-position: 0 -220px;
  }
}

/*footer*/

.footer-top {
  background-color: #191919;
  padding-top: 42px;
  padding-bottom: 25px;
}

.footer-info {
  width: 45%;
}

.footer-logo-out {
  padding-bottom: 30px;
}

.about-waxom {
  line-height: 24px;
  color: $light-grey;
  padding-bottom: 32px;
}

.about-waxom-link {
  line-height: 20px;
  color: #d1d1d1;
  padding-right: 10px;
  @include transition-mixin();

  &:after {
    content: "";
    position: absolute;
    top: 6px;
    right: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 0 4px 5px;
    border-color: transparent transparent transparent #d1d1d1;
    @include transition-mixin();
  }

}

@-webkit-keyframes arrow {
  0%   {left:82px; top:6px;}
  50%  {left:87px; top:6px;}
  100% {left:82px; top:6px;}
}

.recent-posts {
  width: 45%;

  .recent-title {
    font-family: Raleway-SemiBold, sans-serif;
    padding-bottom: 10px;
  }

  .post {
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 1px solid $light-grey;

    .post-link {

      .data {
        font-size: 12px;
        line-height: 20px;
        color: $light-grey;
        padding-bottom: 10px;
        @include transition-mixin();
      }

      .recent-text {
        line-height: 20px;
        color: $white;
        @include transition-mixin();
      }
    }
  }
}

.footer-bottom {
  background-color: $black;
  padding-bottom: 30px;
}

.footer-copyright,
.footer-home-link,
.footer-link,
.data-text{
  font-size: 13px;
  color: $light-grey;
}

.footer-home-link {
  color: #959595;
}

.footer__menu {
  margin-left: 12px;
  display: inline-block;

  .footer__menu--item {
    display: inline-block;
    border-left: 1px solid $grey;

    .footer-link {
      display: inline-block;
      vertical-align: top;
      padding-left: 12px;
      padding-right: 12px;
      @include transition-mixin();
    }
  }
}

.data-text:not(:last-child){
  border-right: 1px solid $grey;
}

.footer-link-right {
  font-size: 13px;
  color: #959595;
  display: inline-block;
  padding-left: 12px;
  padding-right: 12px;
  @include transition-mixin();
}

.data-text:before {
  content: attr(data-label);
}

