@mixin borderRadius ($radius: 5px) {
  border-radius: $radius;
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
}

@mixin css3Prefix($property, $value){
  -webkit-#{$property}: #{$value};
  -moz-#{$property}: #{$value};
  -o-#{$property}: #{$value};
}

@mixin transMixin ($style: all, $type: ease, $duration: .4s, $delay: 0s) {

/*  -webkit-transition: $style $type $duration $delay;
  -moz-transition: $style $type $duration $delay;
  -o-transition: $style $type $duration $delay;
  transition: $style $type $duration $delay;*/

  @include css3Prefix('transition', $style $type $duration $delay);

}


.block {
  background-color: aqua;
  @include borderRadius(20px);
  /*@include transMixin(all, ease-in-out, 8s, .1s);*/
  @include transMixin($style: color);
  height: 200px;
  width: 200px;
}


@mixin css3-prefix($property, $value) {
  -webkit-#{$property}: #{$value};
  -moz-#{$property}: #{$value};
  -ms-#{$property}: #{$value};
  -o-#{$property}: #{$value};
  #{$property}: #{$value};
}

@mixin select-none($select: none) {
  @include css3-prefix('user-select', $select);
}


@mixin transition-mixin($style: all, $duration: .4s, $type: ease, $delay : 0s) {
  @include css3-prefix('transition', $style $duration $type $delay);
}


